<!--直播详情-->
<template>
  <div class="live-detail">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>直播设置</el-breadcrumb-item>
      <el-breadcrumb-item>直播详情</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="container">
      <div class="content-top">
        <div class="title">
          {{liveDetail.liveRoomName}} 
          <span class="living" v-if="liveDetail.liveStatus=='9011'">直播中</span>
          <span v-if="liveDetail.liveStatus=='9010'">暂未直播</span>
          <span v-if="liveDetail.liveStatus=='9012'">直播结束</span>
        </div>
        <div class="time">
          {{liveDetail.startTime}} 至 {{liveDetail.endTime}}
        </div>
      </div>
      <div class="content-main">
        <div class="item">
          <span>直播机构:</span>
          <div>{{liveDetail.orgName}}</div>
        </div>
        <div class="item">
          <span>直播人:</span>
          <div>{{liveDetail.userName}}</div>
        </div>
        <div class="item">
          <span>简介:</span>
          <div>
            {{liveDetail.remark}}
          </div>
        </div>
        <div class="item">
          <span>权限:</span>
          <div>{{lookPower[liveDetail.lookPower]}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props:['id'],
  data(){
    return {
      lookPower:{ "9060":"公开","9061":"私有","9062":"指定机构"},
      liveDetail:{}
    }
  },
  created(){
    this.id && this.initDetail()
  },
  methods:{
    initDetail(){
      const params = {liveRoomId:this.id}
      this.$api.getLiveroomDetail(params).then(res => {
        this.liveDetail = res.data
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.live-detail {
  padding: 32px;
  min-height: 100%;
  height: 100%;
}
.container {
  margin-top: 23px;
  height: calc(100% - 37px);
  background: #fff;
  border-radius: 6px;
  overflow: hidden;
  .content-top {
    height: 120px;
    background: #6b96fe;
    padding: 31px 0 33px 40px;
    .title {
      font-size: 20px;
      color: #fff;
      line-height: 1;
    }
    .title span {
      font-size: 12px;
      padding: 0 4px;
      height: 20px;
      line-height: 20px;
      text-align: center;
      border-radius: 2px;
      border: 1px solid #CEDDFF;
      color: #CEDDFF;
      margin-right: 5px;
    }
    .title span.living{
      color: #F9D905;
      border-color: #F9D905;
    }
    .time {
      color: #fff;
      line-height: 1;
      margin-top: 24px;
    }
  }
  .content-main {
    width: 678px;
    padding: 32px 0 0 40px;
    .item {position: relative;margin-bottom: 40px;height:20px}
    .item div {padding-left: 80px;}
    .item span {position: absolute;left: 0;top: 0}
  }
}
</style>